import React, { Component } from 'react'
import { Link } from "react-router-dom";
import Aos from "aos";
import "aos/dist/aos.css";
import axios from 'axios';
const getProductData = () => axios.get('/ds_FoodZone')
    .then((res) => res.data);
export default class FoodZone extends Component {

    redirect() {
        window.location.replace(window.location.origin+ "/mua-ve");
    }
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            color: ['#C19D60', '', '', '', ''],
            numberInit: 1,
            numberEnd: 4,
            flag: [true, false, false, false, false]
        }
    }

    componentDidMount() {
        getProductData().then((res) => {
            this.setState({
                data: res.recordset
            })

        })
        Aos.init({ duration: 1000 });
    }
    handleClick1 = () => {
        if (this.state.flag[0] !== true) {
            this.setState({
                color: ['#C19D60', '', '', '', ''],
                flag: [true, false, false, false, false],
                numberInit: 1,
                numberEnd: 4
            })
        }
    }
    handleClick2 = () => {
        if (this.state.flag[1] !== true) {
            this.setState({
                color: ['', '#C19D60', '', '', ''],
                flag: [false, true, false, false, false],
                numberInit: 5,
                numberEnd: 8
            })
        }
    }
    handleClick3 = () => {
        if (this.state.flag[2] !== true) {
            this.setState({
                color: ['', '', '#C19D60', '', ''],
                flag: [false, false, true, false, false],
                numberInit: 9,
                numberEnd: 11
            })
        }
    }
    handleClick4 = () => {
        if (this.state.flag[3] !== true) {
            this.setState({
                color: ['', '', '', '#C19D60', ''],
                flag: [false, false, false, true, false],
                numberInit: 12,
                numberEnd: 13
            })
        }
    }
    handleClick5 = () => {
        if (this.state.flag[4] !== true) {
            this.setState({
                color: ['', '', '', '', '#C19D60'],
                flag: [false, false, false, false, true],
                numberInit: 14,
                numberEnd: 17
            })
        }
    }
    render() {
        console.log(this.state.data);
        var dem = 0;
        return (
            <div>
                <div className="food-food">
                    <header className="section-header food-zone">
                        <div className="container food-container">
                            <nav className="navbar navbar-expand-lg navbar-light bg-light background-black">
                                <a className="navbar-brand" href>SANJALIKA FOOD</a>
                                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon" />
                                </button>
                                <div className="collapse navbar-collapse" id="navbarNav">
                                    <ul className="navbar-nav menu-right">
                                        <li className="nav-item active">
                                            <Link to="/" className="nav-link">HOME</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link to="" className="nav-link">NEWS</Link>
                                        </li>
                                        <li className="nav-item">
                                            <div className="menu-product">
                                                <Link to="" className="nav-link layer1">EXPERIENCE</Link>
                                                <div className="layer2">
                                                    <ul>
                                                        <li><Link to="/DragonPark">DRAGON PARK</Link></li>
                                                        <li><Link to="/WaterPark">WATER PARK</Link></li>
                                                        <li><Link to="/QueenCableCars">QUEEN CABLE CARS &amp; THE MYSTERIOUS HILLS</Link></li>
                                                        <li><Link to="/LightHouse">SANJALIKA LIGHTHOUSE</Link></li>
                                                        <li><Link to="/Beach">SANJALIKA BEACH</Link></li>
                                                        <li><Link to="/Gallery">SANJALIKA GALLERRY</Link></li>
                                                        <li><Link to="/OldQuarterKiosks">SANJALIKA OLD QUARTER KIOSKS</Link></li>
                                                        <li><Link to="/FoodZone">FOOD ZONE</Link></li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </li>
                                        <li className="nav-item">
                                            <Link to="/AboutUs" className="nav-link">ABOUT US</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link to="/ContactUs" className="nav-link">CONTACT US</Link>
                                        </li>
                                        <li className="nav-item buy-ticket">
                                            <a className="nav-link" onClick={this.redirect}>BUY TICKET</a>
                                        </li>
                                    </ul>
                                </div>
                            </nav>
                            <div className="header-banner">
                                <h2><i>Top Services and Premium Cuisine</i></h2>
                                <h1>Welcome to SANJALIKA FOOD <br /> Restaurant</h1>
                                <Link to="/FoodZoneDetail" className="check-out-menu">Check out our Menu <span className="ml-4"><i className="fas fa-long-arrow-alt-right" /></span></Link>
                            </div>
                        </div>
                    </header>
                    <section className="main">
                        <div className="main-top-food-banner">
                            <div className="container">
                                <div className="row">
                                    <div className="col-sm-6 content-blog-main-top">
                                        <h3 data-aos="fade-up"><i>Our story</i></h3>
                                        <h2 data-aos="fade-up">Few words about us</h2>
                                        <p data-aos="fade-up"  data-aos-delay="200" className="mt-5">Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium
                                        doloremque laudantium totam aperiam. Eaque ipsa quae ab illo inventore veritatis et quasi
                                        architecto beatae vitae dicta sunt. Ut enim ad minima veniam, quis nostrum exercitationem
                        ullam corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur.<br /></p>
                                        <p data-aos="fade-up"  data-aos-delay="200" className="mb-5">Quis autem vel eum iure reprehenderit qui in ea voluptate velit esse quam nihil
                        molestiae consequatur.</p>
                                        <Link data-aos="fade-up" data-aos-delay="400" to="/FoodZoneDetail" className="check-out-menu">Explore Our Menu <span className="ml-4"><i className="fas fa-long-arrow-alt-right" /></span></Link>
                                    </div>
                                    <div className="col-sm-6 content-img-main-top">
                                        <div className="main-img-content">
                                            <img className="img-fluid" data-aos="fade-up" data-aos-delay="800" src="http://restabook.kwst.net/dark/images/all/3.jpg" alt="" />
                                        </div>
                                        <div className="main-img-collapse">
                                            <img style={{ width: '100%' }} data-aos="fade-up" data-aos-delay="200" src="http://restabook.kwst.net/dark/images/all/8.jpg" alt="" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="main-mid-food-banner">
                            <div className="mid-banner-food-left">
                                <div className="quote-box text-center">
                                    <i className="fas fa-quote-right mb-4"/>
                                    <p>"Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque
                                    laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi
                      accusantium."</p>
                                    <div className="signature mt-4">
                                        <h4>Kowalsky - Restaurant’s cheaf</h4>
                                        <img src="http://restabook.kwst.net/dark/images/signature.png" alt="" />
                                    </div>
                                </div>
                            </div>
                            <div className="mid-banner-food-right">
                                <div className="container">
                                    <div className="column-text">
                                        <div className="column-text-title text-center">
                                            <h5><i>Call For Reservations</i></h5>
                                            <h2>Opening Hours<br /></h2>
                                        </div>
                                    </div>
                                    <div className="work-time">
                                        <div className="row">
                                            <div className="col-sm-6">
                                                <h3>Sunday to Tuesday</h3>
                                                <div className="hour">
                                                    09:00
                            <br />
                            22:00
                          </div>
                                            </div>
                                            <div className="col-sm-6">
                                                <h3>Friday to Saturday</h3>
                                                <div className="hour">
                                                    11:00
                            <br />
                            19:00
                          </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="bold-separator">
                                        <span />
                                    </div>
                                    <div className="big-number-phone text-center">+0963922780</div>
                                </div>
                            </div>
                        </div>
                        <div className="main-mid-choose-us">
                            <div className="container">
                                <h5 className="text-center pt-5"><i>Why people choose us</i></h5>
                                <h2 className="text-center">Prepare for first-class service<br /></h2>
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="content-inner">
                                            <div className="content-front">
                                                <div className="cf-inner">
                                                    <div className="bg" style={{ backgroundImage: 'url(http://restabook.kwst.net/dark/images/services/3.jpg)' }}>
                                                    </div>
                                                    <div className="overlay"> </div>
                                                    <div className="inner">
                                                        <h2>Creative &amp; Talented Chefs</h2>
                                                        <h4>Hot &amp; ready to serve</h4>
                                                    </div>
                                                    <div className="serv-num">01.</div>
                                                </div>
                                            </div>
                                            <div className="content-food-back">
                                                <div className="cf-inner">
                                                    <div className="inner">
                                                        <div className="dec-icon">
                                                            <i className="fas fa-utensils" />
                                                        </div>
                                                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas in pulvinar
                                neque. Nulla finibus lobortis pulvinar.</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="content-inner">
                                            <div className="content-front">
                                                <div className="cf-inner">
                                                    <div className="bg" style={{ backgroundImage: 'url(http://restabook.kwst.net/dark/images/services/2.jpg)' }}>
                                                    </div>
                                                    <div className="overlay"> </div>
                                                    <div className="inner">
                                                        <h2>Fresh Ingredient, Meals</h2>
                                                        <h4>Quality is the heart</h4>
                                                    </div>
                                                    <div className="serv-num">02.</div>
                                                </div>
                                            </div>
                                            <div className="content-food-back">
                                                <div className="cf-inner">
                                                    <div className="inner">
                                                        <div className="dec-icon">
                                                            <i className="fas fa-carrot" />
                                                        </div>
                                                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas in pulvinar
                                neque. Nulla finibus lobortis pulvinar.</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="content-inner">
                                            <div className="content-front">
                                                <div className="cf-inner">
                                                    <div className="bg" style={{ backgroundImage: 'url(http://restabook.kwst.net/dark/images/services/1.jpg)' }}>
                                                    </div>
                                                    <div className="overlay"> </div>
                                                    <div className="inner">
                                                        <h2>Daily New Fresh Menus</h2>
                                                        <h4>Start eating better</h4>
                                                    </div>
                                                    <div className="serv-num">03.</div>
                                                </div>
                                            </div>
                                            <div className="content-food-back">
                                                <div className="cf-inner">
                                                    <div className="inner">
                                                        <div className="dec-icon">
                                                            <i className="fas fa-fish" />
                                                        </div>
                                                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas in pulvinar
                                neque. Nulla finibus lobortis pulvinar.</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="menu-food-banner">
                            <div className="menu-food">
                                <div className="menu-food-banner-title text-center">
                                    <h5><i>Special menu offers.</i></h5>
                                    <h2>Enjoy Restaurants Specialties<br /></h2>
                                </div>
                            </div>
                            <div className="menu-special">
                                <div className="container food-menu-container">
                                    <div className="row" data-aos="fade-up" data-aos-delay="300">
                                        <div className="col-sm-4 menu-special-left">
                                            <ul>
                                                <li style={{ color: this.state.color[0] }} onClick={() => this.handleClick1()}><span>01.</span> Main dishes</li>
                                                <li style={{ color: this.state.color[1] }} onClick={() => this.handleClick2()}><span >02.</span> Starter</li>
                                                <li style={{ color: this.state.color[2] }} onClick={() => this.handleClick3()}><span >03.</span>Desserts</li>
                                                <li style={{ color: this.state.color[3] }} onClick={() => this.handleClick4()}><span >04.</span> Sea Food</li>
                                                <li style={{ color: this.state.color[4] }} onClick={() => this.handleClick5()}><span >05.</span> Drinks</li>
                                            </ul>
                                        </div>
                                        <div className="col-sm-8 menu-special-right">
                                            <ul>
                                                {this.state.data.map((val, key) => {
                                                    if (this.state.numberInit <= val.ID && val.ID <= this.state.numberEnd) {
                                                        dem++;
                                                        return (<li key={key}><span>0{dem}. <h5>{val.Title}</h5><p>----------------------------------------------------</p><h6>${val.Price}</h6></span><p>{val.Content}</p></li>)
                                                    }
                                                    else {
                                                        return '';
                                                    }
                                                })}
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    <div className="food-footer">
                        <div className="container menu-food-footer">
                            <ul className="inner-footer">
                                <li><Link to="/">HOME</Link></li>
                                <li><Link to="/AboutUs">ABOUT US</Link></li>
                                <li><Link to="#">ACTIVITIES</Link></li>
                                <li><Link to="/ContactUs">CONTACT US</Link></li>
                                <li><Link to="/News">NEWS</Link></li>
                                <li><Link to="#">MAPS</Link></li>
                                <li><Link to="#">SUPPORT</Link></li>
                            </ul>
                            <h1 className="text-center">SANJALIKA WATER PARK</h1>
                            <div className="footer-three-icon">
                                <div className="home-three-icon">
                                    <Link to="" className="icon-twitter"><i className="fab fa-twitter" /></Link>
                                </div>
                                <div className="home-three-icon">
                                    <Link to="" className="icon-facebook"><i className="fab fa-facebook-f" /></Link>
                                </div>
                                <div className="home-three-icon">
                                    <Link to="" className="icon-instagram"><i className="fab fa-instagram" /></Link>
                                </div>
                            </div>
                            <h4 className="text-center"><span style={{ color: 'white', fontWeight: 'bold' }}>SANJALIKA</span> © 2021 All Rights Reserved.</h4>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
